import React from 'react';
import './App.css';
import Navbar from '../Navbar/Navbar.js';

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

export default class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <Router basename={process.env.PUBLIC_URL}>
        <Navbar />

        <Switch>
          <Route exact path="/" render={() => <h1>Home</h1>} />
          <Route path="/demos" render={() => <h1>Demos</h1>} />
          <Route path="/certificates" render={() => <h1>Certificates</h1>} />
          <Route path="/follow" render={() => <h1>Follow</h1>} />
          <Route status={404} render={()=> <h1>404 not found!</h1>} />
        </Switch>
      </Router>
    );
  }
}